import { createActions } from 'reduxsauce'

export const { Types, Creators } = createActions({
  addPost: ['post'],
  editPost: ['postId'],
  deletePost: ['postId'],
  upVote: ['postId'],
  downVote: ['postId'],
  getPost: ['post'],

  addCommet: ['commet'],
  editCommet: ['commetId'],
  deleteCommmet: ['commetId'],

  initialData: ['posts', 'filter'],
  requestData: null,
  dataReceived: null,

  receiveCategories: ['categories'],
  selectCategory: ['category'],

  sortPost: ['term'],

  updateList: ['posts']
})
export default Creators
